"use client";
import { useState, useEffect } from "react";
import { BiSearch } from "react-icons/bi";
import useAdminStore from "@/store/adminStore";
import toast from "react-hot-toast";

export default function DonationDataTable() {
  const { donations, isLoading, getDonations } = useAdminStore();
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    const fetchDonations = async () => {
      try {
        await getDonations({ limit: 100 });
      } catch (error) {
        console.error("Failed to load donations:", error);
        toast.error("Failed to load donations");
      }
    };

    fetchDonations();
  }, [getDonations]);

  // Filter donations based on search
  const filteredDonations = donations.filter(donation =>
    (donation.donor?.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
     donation.donor?.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
     donation.donorDetails?.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
     donation.donorDetails?.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
     donation.campaign?.title?.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  // Pagination
  const indexOfLastEntry = currentPage * entriesPerPage;
  const indexOfFirstEntry = indexOfLastEntry - entriesPerPage;
  const currentEntries = filteredDonations.slice(indexOfFirstEntry, indexOfLastEntry);
  const totalPages = Math.ceil(filteredDonations.length / entriesPerPage);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <>
      {/* Page Header */}
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Donation Recieved Data</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li>
              <a href="/admin" className="hover:underline text-gray-600 hover:text-blue-600">
                Home
              </a>
            </li>
            <li className="text-gray-400">/</li>
            <li>
              <a href="/admin/donations" className="hover:underline text-gray-600 hover:text-blue-600">
                Donation Data
              </a>
            </li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">All Donation Transactions</li>
          </ol>
        </nav>
      </div>

      {/* Data Table Card */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
        <h2 className="text-xl font-semibold text-gray-900 mb-6">Donation Data Table</h2>

        {/* Table Controls */}
        <div className="flex items-center justify-between mb-6">
          {/* Entries per page */}
          <div className="flex items-center gap-2">
            <select
              value={entriesPerPage}
              onChange={(e) => {
                setEntriesPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
            <span className="text-sm text-gray-600">entries per page</span>
          </div>

          {/* Search */}
          <div className="relative">
            <input
              type="text"
              placeholder="Search..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setCurrentPage(1);
              }}
              className="border border-gray-300 rounded-lg pl-4 pr-10 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 w-64"
            />
            <BiSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
          </div>
        </div>

        {/* Table */}
        {isLoading ? (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500 mx-auto"></div>
            <p className="text-gray-600 mt-4">Loading donations...</p>
          </div>
        ) : (
          <>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="bg-gray-50 border-b border-gray-200">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Name Of Donor
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Donor Email
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Donor Mobile No.
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Donation For
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Donation Date
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Amount Donated
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Payment Method
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">
                      Status
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {currentEntries.length > 0 ? (
                    currentEntries.map((donation, index) => (
                      <tr
                        key={donation._id}
                        className={`border-b border-gray-100 hover:bg-gray-50 transition-colors ${
                          index % 2 === 0 ? 'bg-white' : 'bg-gray-50/50'
                        }`}
                      >
                        <td className="px-4 py-3 text-sm text-gray-900">
                          {donation.isAnonymous ? 'Anonymous' : (donation.donor?.name || donation.donorDetails?.name || 'N/A')}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-700">
                          {donation.isAnonymous ? 'Hidden' : (donation.donor?.email || donation.donorDetails?.email || 'N/A')}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-700">
                          {donation.isAnonymous ? 'Hidden' : (donation.donorDetails?.phone || 'N/A')}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-700">
                          {donation.campaign?.title || 'General Donation'}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-700">
                          {new Date(donation.createdAt).toLocaleDateString()}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-900 font-medium">
                          â‚¹{donation.amount?.toLocaleString() || 0}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-700 uppercase">
                          {donation.paymentMethod || donation.paymentDetails?.gateway || 'N/A'}
                        </td>
                        <td className="px-4 py-3">
                          <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                            donation.status === 'completed' ? 'bg-green-100 text-green-800' :
                            donation.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                            donation.status === 'failed' ? 'bg-red-100 text-red-800' :
                            'bg-gray-100 text-gray-800'
                          }`}>
                            {donation.status}
                          </span>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="8" className="px-4 py-8 text-center text-gray-500">
                        {isLoading ? 'Loading donations...' : 'No donations found'}
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>

            {/* Pagination */}
            <div className="flex items-center justify-between mt-6">
              <div className="text-sm text-gray-600">
                Showing {indexOfFirstEntry + 1} to {Math.min(indexOfLastEntry, filteredDonations.length)} of {filteredDonations.length} entries
              </div>

              <div className="flex items-center gap-2">
                {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
                  <button
                    key={page}
                    onClick={() => paginate(page)}
                    className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                      currentPage === page
                        ? 'bg-gray-200 text-gray-900'
                        : 'text-gray-600 hover:bg-gray-100'
                    }`}
                  >
                    {page}
                  </button>
                ))}
                {totalPages > 1 && (
                  <button
                    onClick={() => paginate(Math.min(currentPage + 1, totalPages))}
                    disabled={currentPage === totalPages}
                    className="px-3 py-1 text-sm text-gray-600 hover:text-gray-900 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    â€º
                  </button>
                )}
              </div>
            </div>
          </>
        )}
      </div>
    </>
  );
}



